let app = require("@/config");
export default {
	onLoad() {
		this.avatar = app.userInfo.avatar;
		this.currentUserGuid = app.userInfo.token;
	},
	onShow(){
		this.GetInfo();
	},
	data() {
		return {
			avatar:'',
			currentUserGuid:'',
			userInfo:''
		};
	},
	methods: {
		GetInfo () {
			let currentUserGuid = this.currentUserGuid;
			uni.post("/uc/GetUserInfo",{currentUserGuid},msg=>{
				if(msg.success) {
					this.userInfo = msg.data;
					uni.setStorage({
						key:'userInfo',
						data:msg.data,
					});
				}
			}
			)
		}
	},
	
}
